import { Field, InputType } from "@nestjs/graphql";
import { IsBoolean, IsNotEmpty, IsNumber } from "class-validator";


@InputType()
export class PaypalSubscriptionTaxes {

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  percentage: number;

  @Field(() => Boolean)
  @IsNotEmpty()
  @IsBoolean()
  inclusive: boolean;
  
}
